.container {
  max-width: 1408px;
  width: 100%;
  padding: 0 24px;
  margin: 0 auto;
  display: block;

  &--sm {
    max-width: 1148px;
  }
}

.h1 {
  font-size: 48px;
  line-height: 1.2;

  @media screen and (max-width: $lg) {
    font-size: 32px;
  }
}

.h2 {
  font-size: 24px;
  line-height: 1.4;
}

.h3 {
  font-size: 20px;
  line-height: 1.4;
}
.h1, .h2, .h3 {
  color: $text;
  font-weight: 700;
}

.text {
  color: $subText;
  font-weight: 500;
}

.text-xl {
  font-weight: 700;
  font-size: 70px;
  line-height: 1.1;
  color: $text;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: $primary;
}

.flex {
  display: flex;
}

.flex-1 {
  flex: 1;
}

.align-center  {
  align-items: center;
}

.space-between {
  justify-content: space-between;
}

.section {
  padding: 120px 0;

  @media screen and (max-width: $lg) {
    padding: 64px 0;
  }
}

.bg-blue {
  background-color: $bg-blue;
}
.bg-lightblue {
  background-color: $bg-lightblue;
}

.overflow-hidden {
  overflow: hidden;
}

.show-lg {
  display: block;

  @media screen and (max-width: $lg) {
    display: none;
  }
}

.hide-lg {
  display: none;

  @media screen and (max-width: $lg) {
    display: block;
  }
}

.mb-32 {
  margin-bottom: 32px;
}

.btn {
  display: block;
  text-align: center;
  background-color: $primary;
  color: $white;
  padding: 12px;
  border-radius: 10px;
  width: 100%;
  font-size: 18px;
  line-height: 1.6;
  font-weight: 500;

  &:hover {
    opacity: 0.8;
  }

  &--disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}

.footer {
  background-color: $dark;
  padding: 22px;
  text-align: center;
  font-size: 15px;
  line-height: 1.4;
  color: $white;
}