@import 'reset/reset';
@import 'variables';
@import 'media';
@import 'elementary';
@import 'form';

*,
*:after,
*:before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

html {
  font-size: 14px;
  line-height: 1;
}

body {
  background-color: $bg-main;
  font-family: 'Poppins', sans-serif;
}

.link {
  color: $subText;
  font-size: 1rem;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0;

  &__logo {
    display: block;
  }

  &__menu-toggle {
    margin-right: 16px;
    display: none;
  }

  &__collapsable {
    display: flex;
    align-items: center;
  }

  &__item {
    color: $subText;
    font-size: 1rem;
    padding: 8px;
    display: block;
    text-align: center;

    + .navbar__item {
      margin-left: 20px;
    }

    &:hover {
      opacity: 0.8;
    }

    &--focused {
      border: 1px solid $primary;
      color: $primary;
      border-radius: 6px;
      padding: 8px 20px;
    }
  }

  @media screen and (max-width: $sm) {
    flex-direction: column;
    align-items: start;

    &__logo {
      width: 130px;
      display: flex;
      align-items: center;
    }

    &__menu-toggle {
      display: block;
    }

    &__collapsable {
      display: none;
    }
  }
}

.hero {
  max-width: 1360px;
  margin: 0 24px;
  margin: 0 auto;
  position: relative;
  margin-bottom: -100px;

  &__text-container {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 50%;
    transform: translateY(50%);
  }

  &__inner {
    margin-left: 100px;
  }

  &__text {
    color: white;
    
    &--main {
      line-height: 1;
      margin-bottom: 20px;
    }

    &--sub {
      font-size: 20px;
      line-height: 34px;
      margin-bottom: 20px;
    }
  }

  &__overlay {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-image: linear-gradient(to left, transparent 42%, rgba(18, 27, 33, 0.75));
  }

  @media screen and (max-width: $lg) {
    margin-bottom: 0;

    &__img {
      min-height: 535px;
      object-fit: cover;
    }

    &__inner {
      margin-left: 16px;
    }

    &__text-container {
      bottom: 36px;
      transform: translateY(0);
    }

    &__arrow {
      width: 10px;
    }

    &__text {
      &--main {
        font-size: 40px;
      }

      &--sub {
        font-size: 16px;
      }
    }
  }
}

.features {
  display: flex;
  justify-content: space-between;
  padding-top: 220px;
  padding-bottom: 120px;


  @media screen and (max-width: $lg) {
    padding-top: 64px;
    padding-bottom: 64px;
    display: block;
  }
}

.feature {
  flex: 1;
  max-width: 270px;

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;

    width: 96px;
    height: 96px;
    background-color: $primary;
    box-shadow: 0px 32px 54px 0px rgba(#258AFF, 0.22);

    margin-bottom: 36px;

    img {

    }
  }

  &__header {
    margin-bottom: 20px;
  }

  &__description {
    color: $subText;
    font-weight: 600;
    font-size: 16px;
    line-height: 1.75;
  }

  @media screen and (max-width: $lg) {
    max-width: none;

    +.feature {
      margin-top: 54px;
    }
  }
}

.images {
  margin-top: 50px;
  display: flex;

  &__item {
    position: relative;

    &:nth-child(1) {

      h3 {
        margin-left: 4%;
      }
    }

    &:nth-child(2) {
      margin-top: 14%;
      
      h3 {
        margin-left: 7%;
      }
    }
  
    &:nth-child(3) {
      margin-top: 33%;
  
      h3 {
        margin-left: 7%;
      }
    }
  }

  &__arrow {
    position: absolute;
    left: 73%;
    top: 33%;
    width: 31%;

    &-down {
      position: absolute;
      left: 82%;
      top: 18%;
      width: 75%;
    }
  }

  &__img {
    position: relative;
    left: -15%;
    max-width: none;
    width: 125%;
    margin-bottom: -17%;
  }

  @media screen and (max-width: $lg) {
    display: block;
    margin-top: 32px;

    &__item {

      h3 {
        margin-left: 10% !important;
      }

      &:nth-child(2) {
        margin-top: 20%;

        .images__img {
          left: -12%;
        }
      }
    
      &:nth-child(3) {
        margin-top: 20%;

        .images__img {
          left: -12%;
        }
      }
    }

    &__img {
      left: -10%;
    }

    &__arrow {
      left: 9%;
      top: 102%;

      &-down {
        left: 55%;
        top: 102%;
        transform: scaleX(-1) rotate(13deg);
        width: 39%;
      }
    }
  }
}

