.form {
  max-width: 375px;
  margin: 0 auto;
  margin-top: 94px;

  input {
    display: block;
    width: 100%;
    border: 1px solid $fieldBorder;
    padding: 9px 12px;
    border-radius: 3px;
    font-size: 15px;
    font-weight: 400;
    line-height: 1.8;
    
    + input {
      margin-top: 20px;
    }
  }

  button {
    margin-top: 30px;
  }

  &--disabled {
    opacity: 0.7;
    pointer-events: none;
  }

  @media screen and (max-width: $lg) {
    margin-top: 32px;
  }
}